@import url(https://fonts.googleapis.com/css?family=Lato|Pompiere&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: Lato;
  font-weight: normal;
  font-size: 1.5rem;
  text-align: center;
}

.title {
  padding-top: 1rem;
  padding-left: 1rem;
  font-family: Pompiere;
  font-style: normal;
  font-weight: normal;
  font-size: 4rem;
}

.subtitle {
  padding-left: 4rem;
  font-family: Lato;
  font-style: normal;
  font-weight: normal;
  font-size: 0.875rem;
}

.dataTable {
  padding: 0 1rem;
}

.addNewEntriesContent {
  margin: 1rem 7rem;
}

.dropzone {
  border: 2px dashed black;
  border-radius: 5px;
  text-align: center;
  padding: 1rem;
  margin-bottom: 1rem;
}
